'use client'

import { useUIStore } from '@/lib/stores/ui-store'
import { CreateProjectModal } from '@/components/modals/create-project-modal'
// Import other modals as you create them
// import { CreateTaskModal } from '@/components/modals/create-task-modal'
// import { InviteTeamModal } from '@/components/modals/invite-team-modal'

export function ModalProvider() {
  const { activeModal, setActiveModal } = useUIStore()
  
  const closeModal = () => setActiveModal(null)

  return (
    <>
      <CreateProjectModal
        open={activeModal === 'create-project'}
        onOpenChange={(open) => !open && closeModal()}
      />
      
      {/* Add other modals here as you create them */}
      {/* 
      <CreateTaskModal
        open={activeModal === 'create-task'}
        onOpenChange={(open) => !open && closeModal()}
      />
      
      <InviteTeamModal
        open={activeModal === 'invite-team'}
        onOpenChange={(open) => !open && closeModal()}
      />
      */}
    </>
  )
}